@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
}
.form-wrapper{border:none;display:flex;width:100%;justify-content:space-between}.form-wrapper select,.form-wrapper button,.form-wrapper input,.form-wrapper p{width:30%;padding:10px;margin:10px;background-color:#cecccc;border:none;border-radius:5px;box-shadow:0 0 2px grey}

.list{list-style:none;margin-top:30px;font-size:20px;height:60vh;width:550px;overflow-y:scroll}.list-item{display:flex;justify-content:space-between;background-color:#dfdddd;width:90%;margin:20px auto;border-radius:5px}.list-item span{text-align:start;padding:10px 30px}

.dashboard{display:flex;flex-wrap:wrap;width:90vw;justify-content:space-between;align-items:center}

.section{width:35px;height:35px;background-color:#fff}.blue{background-color:#5BC0EB}.green{background-color:#9BC53D}.red{background-color:#E55934}

.App{padding-top:10px;text-align:center;width:90vw;height:100vh;background-color:lightgreen;margin:auto;display:flex;justify-content:center;flex-wrap:wrap;align-items:flex-start}

